<template>
  <div class="container">

    <div class="row" style="margin-top: 16px;">
      <!-- Sidebar Widgets Column -->
      <div class="col-md-4">
        <div v-if="!!info" class="card">
          <img class="card-img-top" src="https://scontent.fsgn7-1.fna.fbcdn.net/v/t1.0-9/16114598_784143988400871_799956445756955616_n.jpg?_nc_cat=0&oh=b13f7c7e4187a0da2545b35d3b57604a&oe=5B4490D4" alt="Card image cap">
          <div class="card-body">
            <h5 class="card-title">{{info.name}}</h5>
            <p class="card-text">{{info.email}}</p>
          </div>
          <ul class="list-group list-group-flush">
            <li class="list-group-item">Cập Nhật</li>
            <li class="list-group-item">Thiết Lập</li>
          </ul>
        </div>

      </div>

      <!-- Blog Entries Column -->
      <div class="col-lg-8">
        <div class="card">
          <div class="card-body">
            <h5 class="card-title">Thông báo!!!</h5>
            <p class="card-text">Tính năng đang trong giai đoạn phát triển.</p>
            <p class="card-text"><small class="text-muted">3 phút trước</small></p>
          </div>
        </div>
      </div>
    </div>
    <!-- /.row -->

  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {Pagebar} from '@/components'
  export default {
    name: 'dashboard',
    data () {
      return {
        msg: 'Welcome to Dashboard!!!'
      }
    },
    components: {
      Pagebar
    },
    computed: {
      ...mapGetters(['info'])
    }
  }
</script>



