<template>
  <div>
    <div class="portlet light bordered portlet-body" v-if="false">
      <div class="table-toolbar">
        <div class="row">
          <div class="col-md-6">
            <router-link :to="{name: 'CollectionAdd'}" id="sample_editable_1_new" class="btn sbold green">
              <i class="fa fa-plus"></i>
            </router-link>
            <button class="btn sbold blue" @click="getCollection()">
              <i class="fa fa-refresh"></i>
            </button>
          </div>
          <div class="col-md-6">
            <div class="btn-group pull-right">
              <label>
                Show
                <select v-on:change="onChangeSize" :value="collections.per_page" class="form-control input-sm input-xsmall input-inline">
                  <option v-for="option in pageSizeOptions" v-bind:value="option">{{option}}</option>
                </select>
              </label>
            </div>
          </div>
        </div>
      </div>
      <div class="dataTables_wrapper no-footer">
        <div class="table-scrollable table-responsive">
          <table class="table table-hover text-center">
            <thead>
            <tr>
              <th> # </th>
              <th style="text-align: center"> Name </th>
              <th style="text-align: center"> Description </th>
              <th style="text-align: center"> Duration</th>
              <th style="text-align: center"> Questions</th>
              <th style="text-align: center"> Status</th>
              <th style="text-align: center"> Image</th>
              <th style="text-align: center"> Actions </th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in collections.data">
              <td>{{item.id}}</td>
              <td>{{item.name}}</td>
              <td>{{item.description}}</td>
              <td>{{moment.duration(item.time, 'seconds').humanize()}}</td>
              <td>{{item.questions_count}}</td>
              <td>
                <span :class="['label label-sm label-mini', item.isPublish ? 'label-success' : 'label-warning']"> {{item.isPublish ? 'Published' : 'Draft'}} </span>
              </td>
              <td>
                <img v-if="item.image" :src="item.image" :alt="item.name" class="img img-thumbnail" style="max-width: 150px"/>
              </td>
              <td>
                <div class="btn-group-vertical">
                  <router-link :to="{name: 'CollectionDetail', params: {id: item.id}}" type="button" class="btn purple">
                    <i class="fa fa-eye"></i>
                  </router-link>
                  <router-link :to="{name: 'CollectionEdit', params: {id: item.id}}" type="button" class="btn green">
                    <i class="fa fa-pencil"></i>
                  </router-link>
                  <!-- <button type="button" class="btn red" @click="deleteCollection(item.id)">
                    <i class="fa fa-trash"></i>
                  </button> -->
                  <button type="button" class="btn blue" @click="publishCollection({ids: [item.id], publish: !item.isPublish})">
                    <i :class="[{fa: true}, !item.isPublish ? 'fa fa-unlock' : 'fa fa-lock']"></i>
                  </button>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="row">
        <div class="col-md-5 col-sm-5">
          <div class="dataTables_info" id="sample_1_info" role="status" aria-live="polite">
            Showing {{collections.from}} to {{collections.to}} of {{collections.total}} records
          </div>
        </div>
        <div class="col-md-7 col-sm-7">
          <div v-if="collections.total / parseInt(collections.per_page, 10) > 1" class="dataTables_paginate paging_bootstrap_full_number pull-right" id="sample_1_paginate">
            <ul class="pagination" style="visibility: visible;">
              <li class="prev">
                <a @click="collections.current_page !== 1 && updatePageCollection(1)"title="First">
                  <i class="fa fa-angle-double-left"></i>
                </a>
              </li>
              <li class="prev">
                <a @click="collections.current_page > 1 && updatePageCollection(collections.current_page-1)" title="Prev">
                  <i class="fa fa-angle-left"></i>
                </a>
              </li>
              <li v-for="n in collections.last_page"  :class="[collections.current_page === n ? 'active' : '']">
                <a @click.prevent="updatePageCollection(n)">{{n}}</a>
              </li>
              <li class="next">
                <a @click="collections.current_page < collections.last_page && updatePageCollection(collections.current_page+1)"  title="Next">
                  <i class="fa fa-angle-right"></i>
                </a>
              </li>
              <li class="next">
                <a @click="collections.current_page !== collections.last_page && updatePageCollection(collections.last_page)" title="Last">
                  <i class="fa fa-angle-double-right"></i>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>

    </div>

    <!-- Page Content -->
    <div class="container">

      <div class="row">

        <!-- Blog Entries Column -->
        <div class="col-md-8">

          <h1 class="my-4">Đề Thi
            <small>mới nhất</small>
          </h1>

          <!-- Blog Post -->
          <div class="card mb-4" v-for="collection in collections.data">
            <!--<img class="card-img-top" src="http://placehold.it/750x300" alt="Card image cap">-->
            <div class="card-body">
              <h2 class="card-title">{{ collection.name }}</h2>
              <p class="card-text">{{ collection.description }}</p>
              <router-link :to="{name: 'CollectionDetail', params: {id: collection.slug}}" class="btn btn-primary">
                Chi tiết &rarr;
              </router-link>
            </div>
            <div class="card-footer text-muted">
              {{moment.unix(collection.created_at).fromNow()}}
              <!--<router-link :to="{name: 'CollectionDetail', params: {id: 3}}">-->
                <!--Huy Bui-->
              <!--</router-link>-->
            </div>
          </div>

          <!-- Pagination -->
          <nav aria-label="Page navigation example" v-if="collections.total / parseInt(collections.per_page, 10) > 1">
            <ul class="pagination">
              <li class="page-item">
                <a @click="collections.current_page > 1 && updatePageCollection(collections.current_page-1)"  class="page-link" href="#" aria-label="Previous">
                  <span aria-hidden="true">&laquo;</span>
                  <span class="sr-only">Previous</span>
                </a>
              </li>
              <li v-for="n in collections.last_page"  :class="[collections.current_page === n ? 'page-item active' : 'page-item']">
                <a @click.prevent="updatePageCollection(n)" class="page-link">{{n}}</a>
              </li>

              <li class="page-item">
                <a @click="collections.current_page < collections.last_page && updatePageCollection(collections.current_page+1)"   class="page-link" href="#" aria-label="Next">
                  <span aria-hidden="true">&raquo;</span>
                  <span class="sr-only">Next</span>
                </a>
              </li>
            </ul>
          </nav>

          <div v-if="collections.total / parseInt(collections.per_page, 10) > 1" class="dataTables_paginate paging_bootstrap_full_number pull-right" id="sample_1_paginate">
            <ul class="pagination" style="visibility: visible;">
              <li class="prev">
                <a @click="collections.current_page !== 1 && updatePageCollection(1)"title="First">
                  <i class="fa fa-angle-double-left"></i>
                </a>
              </li>
              <li class="prev">
                <a @click="collections.current_page > 1 && updatePageCollection(collections.current_page-1)" title="Prev">
                  <i class="fa fa-angle-left"></i>
                </a>
              </li>
              <li v-for="n in collections.last_page"  :class="[collections.current_page === n ? 'active' : '']">
                <a @click.prevent="updatePageCollection(n)">{{n}}</a>
              </li>
              <li class="next">
                <a @click="collections.current_page < collections.last_page && updatePageCollection(collections.current_page+1)"  title="Next">
                  <i class="fa fa-angle-right"></i>
                </a>
              </li>
              <li class="next">
                <a @click="collections.current_page !== collections.last_page && updatePageCollection(collections.last_page)" title="Last">
                  <i class="fa fa-angle-double-right"></i>
                </a>
              </li>
            </ul>
          </div>

        </div>

        <!-- Sidebar Widgets Column -->
        <div class="col-md-4">

          <!-- Search Widget -->
          <div class="card my-4">
            <h5 class="card-header">Tìm kiếm</h5>
            <div class="card-body">
              <div class="input-group">
                <input type="text" class="form-control" placeholder="Nhập từ khoá...">
                <span class="input-group-btn">
                  <button class="btn btn-secondary" type="button">Tìm</button>
                </span>
              </div>
            </div>
          </div>

          <!-- Categories Widget -->
          <div class="card my-4">
            <h5 class="card-header">Nhãn</h5>
            <div class="card-body">
              <div class="row">
                <div class="col-lg-6">
                  <ul class="list-unstyled mb-0">
                    <li>
                      <a href="#">Web Design</a>
                    </li>
                    <li>
                      <a href="#">HTML</a>
                    </li>
                    <li>
                      <a href="#">Freebies</a>
                    </li>
                  </ul>
                </div>
                <div class="col-lg-6">
                  <ul class="list-unstyled mb-0">
                    <li>
                      <a href="#">JavaScript</a>
                    </li>
                    <li>
                      <a href="#">CSS</a>
                    </li>
                    <li>
                      <a href="#">Tutorials</a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>

          <!-- Side Widget -->
          <div class="card my-4">
            <h5 class="card-header">Thông báo!</h5>
            <div class="card-body">
              Chào mừng bạn abcxyz!!!
            </div>
          </div>

        </div>

      </div>
      <!-- /.row -->

    </div>
    <!-- /.container -->
  </div>
</template>
<script>
  import {mapActions, mapGetters} from 'vuex'
  export default {
    data () {
      return {
        pageSizeOptions: [
          5,
          10,
          20,
          50,
          100
        ]
      }
    },
    created: function () {
      // if (this.collections.current_page < 0) {
      //   this.updatePageCollection(1)
      // }
      this.updatePageCollection(1)
    },
    computed: mapGetters([
      'collections'
    ]),
    methods: {
      ...mapActions([
        'getCollection',
        'updateSizeCollection',
        'updatePageCollection',
        'deleteCollection',
        'publishCollection'
      ]),
      onChangeSize (event) {
        this.updateSizeCollection(event.srcElement.value)
      }
    }
  }
</script>



